.dropdown {
  display: inline-block;
}
.carBrand {
  margin: 0;
  color: #8a8a89;
  font-family: Manrope;
  font-size: 14px;
  font-style: normal;
  font-weight: 500;
  line-height: 18px;
  margin-bottom: 8px;
}

.dropbtn {
  display: flex;

  padding-left: 18px;
  justify-content: left;
  align-items: center;

  border-radius: 14px;
  border: none;
  background: #f7f7fb;
  width: 224px;
  height: 48px;

  color: #121417;

  font-size: 18px;
  font-style: normal;
  font-weight: 500;
  line-height: 1.11;
}

/* Стили для содержимого выпадающего списка */
.dropdownContent {
  display: none;
  position: absolute;
  min-width: 224px;
  z-index: 1;
  max-height: 272px;
  overflow-y: auto;
  border-radius: 14px;
  border: 1px solid rgba(18, 20, 23, 0.05);
  background: #fff;
  box-shadow: 0px 4px 36px 0px rgba(0, 0, 0, 0.02);
  padding-top: 14px;
}

.dropdownContent::-webkit-scrollbar {
  width: 12px; /* Ширина полосы прокрутки */
  padding-left: 4px;
}

.dropdownContent::-webkit-scrollbar-thumb {
  border-radius: 10px;

  margin-right: 10px;

  background: rgba(18, 20, 23, 0.05);
}

.dropdownContent::-webkit-scrollbar-thumb:hover {
  background: rgba(0, 0, 0, 0.295);
}

/* Стили для ссылок в списке */
.dropdownContent a {
  color: black;
  padding: 12px 16px;
  text-decoration: none;
  display: block;

  color: rgba(18, 20, 23, 0.2);

  font-size: 16px;
  font-style: normal;
  font-weight: 500;
  line-height: 1.25;
}

/* Стили для ссылок при наведении курсора */
.dropdownContent a:hover {
  background-color: none;
  color: black;
}

/* Отображение выпадающего списка при наведении на кнопку */
.dropdown:hover .dropdownContent {
  display: block;
}
